@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  height: 100vh;
  background-image: linear-gradient(#e5eaf0, #0552c6);
  background-attachment: scroll;
  display: flex;
  align-items: center;
  justify-content: center;
}
* {
  margin: 0;
  padding: 0;
}
.center-container {
  height: 500px;
  width: 500px;
  padding-top: 10px;
  border-radius: 1rem;
}
.app-heading {
  padding: 4px;
  color: white;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  text-transform: uppercase;
}
.input-container {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}
.input-box-todo {
  margin-left: 10px;
  width: 80%;
  height: 50px;
  border-radius: 14px;
  border: 1px solid black;
  padding-left: 10px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
  outline: none;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: large;
}
.add-btn {
  width: 110px;
  height: 50px;
  border-radius: 1rem;
  border: none;
  background-color: #316fc1;
  color: white;
  font-weight: bolder;
  font-size: larger;
  margin-left: 10px;
  cursor: pointer;
  box-shadow: 0px 5px 10px rgba(11, 10, 10, 0.3);
  transition: 0.5s;
}
.add-btn:hover {
  background-color: #1f1e1e;
}
.add-btn:active {
  box-shadow: none;
}
.list-container {
  display: flex;
  align-items: center;
  margin-top: 5px;
}
.list-item {
  list-style-type: none;
  color: rgb(0, 4, 8);
  padding: 3px;
  padding-left: 5px;
  border-radius: 5px;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  position: relative;
  margin-top: 20px;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
}
.icons {
  position: absolute;
  right: 0px;
  width: 80px;
  background-color: #316fc1;
  text-align: center;
  border-radius: 0.5rem;
  font-weight: bold;
}
.icons p {
  margin: 4px;
  font-size: medium;
  cursor: pointer;
  transition: 0.1s;
  color: #ffff;
}
.icons p:hover {
  color: black;
  transition: 0.5s;
}
.icon-delete:hover {
  color: red;
}
.app-heading {
  text-align: center;
}
@media screen and (max-width: 375px) {
  .center-container {
    height: 600px;
    width: 350px;
    padding-top: 10px;
    border-radius: 1rem;
  }
  .input-box-todo {
    width: 65%;
    height: 50px;
  }
}
